<template>
	<div class="meal-content public-info">
		<div class="meal-content-title"><i class="bar bar-red"></i> {{title}}</div>
		<div class="meal-content-body">
			<div class="meal-content-item" :key="item.id" v-for="item in server">
				<div class="meal-content-item-title">{{item.title}}</div>
				<div class="meal-content-item-conent">
					<ul :class="['conent-column-' + (multiple || '1')]">
						<li :key="list.id" v-for="(list, index) in item.list"><div class="num">{{index+1}}</div><div class="c">{{list}}</div></li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['title', 'data', 'multiple'],
		data() {
			return {
				server: []
			}
		},
		watch: {
			data(newValue) {
				this.server = newValue;
			}
		}
	}
</script>