<template>
	<div class="meal-detail">
		<Meal :data="mealData" />
		<MealServer :title="'服务内容'" :data="mealServerData" multiple="2" />
		<MealServer :title="'预定须知'" :data="mealAppointmentData" />
		<MealServer :title="'体检流程及注意事项'" :data="mealFlowData" />
	</div>
</template>

<script>
	import Meal from './Meal.vue'
	import MealServer from './MealServer.vue'

	export default {
		components: { Meal, MealServer },
		data() {
			return {
				mealData: {},
				mealServerData: [],
				mealAppointmentData: [],
				mealFlowData: []
			}
		},
		created() {
			fnNeeds(this);
			fnMeal(this, {
				id: this.$route.query.id
			});
		}
	}

	function fnNeeds(that, params) {
		that.$request.needs(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.mealServerData = result.server;
				that.mealAppointmentData = result.appointment;
				that.mealFlowData = result.flow;
			}
		})
	}

	function fnMeal(that, params) {
		that.$request['meal' + params.id](params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.mealData = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../../styles/meal/meal-detail.scss';
</style>